import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	components: {
		uniNumberBox,
		uniSwipeAction,
		uniSwipeActionItem
	},
	data() {
		return {
			options: [{
				text: '删除',
				style: {
					backgroundColor: '#FE7956'
				}
			}],
			total: 0, //总价格
			saveMoney:0,//节省钱
			totalNum:0,//总件数
			allChecked: false, //全选状态  true|false
			cartList: [{
					attr_val: "春装款 L",
					checked: false,
					id: 1,
					image: "http://ttianxuan.local/static/upload/images/goods/2019/01/14/1547453895416529.jpg",
					loaded: "loaded",
					number: 1,
					price: 278,
					stock: 15,
					title: "OVBE 长袖风衣",
					save:12,
				},
				{
					attr_val: "春装款 L",
					checked: false,
					id: 2,
					image: "http://ttianxuan.local/static/upload/images/goods/2019/01/14/1547453895416529.jpg",
					loaded: "loaded",
					number: 1,
					price: 278,
					stock: 15,
					title: "OVBE 长袖风衣",
					save:12,
				}
			]
		}
	},
	onShow : function() {
			var loginRes = this.checkLogin('2');
			if(!loginRes){return false;}
		},
	methods: {
		onClick(e) {
			console.log('当前点击的是第' + e.index + '个按钮，点击内容是' + e.content.text)
		},
		change(open) {
			console.log('当前开启状态：' + open)
		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//商品数量
		numberChange($event, index) {
			this.cartList[index].number = $event;
			this.calcTotal();
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			// if(list.length === 0){
			// 	this.empty = true;
			// 	return;
			// }
			let total = 0;
			let totalNum = 0;
			let saveMoney = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.price * item.number;
					totalNum += item.number * 1;
					saveMoney += item.save * item.number;
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
			this.totalNum =totalNum
			this.saveMoney =saveMoney
		}
	}
}
